  flex-direction: column;
}
.c-card__img {
  overflow: hidden;
  display: block;
  height: 270px;
}
.c-card__img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.1s;
}
@media (max-width: 991.9px) {
  .c-card__img {
    height: 180px;
  }
}
.c-card__content {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
  display: flex;
  flex-direction: column;
  padding: 5%;
  border: 1px solid black;
}
@media (max-width: 991.9px) {
  .c-card__content {
    padding: 7%;
    padding-top: 5%;
    text-align: center;
  }
}
.c-card__content__title {
  padding-bottom: 15px;
  position: relative;
}
@media (maax-width: 991.9px) {
  .c-card__content__title {
    padding-bottom: 7px;
  }
}
.c-card__content__title::after {
  content: "";
  display: block;
  position: relative;
  top: 10px;
  width: 50%;
  height: 5px;
  transition: 0.1s;
}
@media (max-width: 991.9px) {
  .c-card__content__title::after {
    width: 60%;
    left: 50%;
    transform: translateX(-50%);
  }
}
.c-card__content__title--red::after {
  background: #a62e19;
}
.c-card__content__title--blue::after {
  background: #4265a8;
}
.c-card__content__title--green::after {
  background: #3e883e;
}
.c-card__content p {
  flex-grow: 1;
  flex-shrink: 0;
  flex-basis: auto;
}
.c-card__content__arrow {
  padding-top: 2rem;
