.main-video {
  height: 48vh;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  transition-property: opacity, background-color, height;
  transition-duration: 1s;
  opacity: 0;
}
.main-video--loaded {
  opacity: 1;
}
.main-video--ready {
  height: 70vh;
  transition-property: opacity, background-color, height;
  transition-duration: 1s;
  background-color: black;
}
.main-video--ready #player::before, .main-video--ready #player::after {
  transition-property: opacity, background-color, height;
  transition-duration: 1s;
  position: absolute;
  content: "";
  top: 0;
  bottom: 0;
  width: 5%;
  z-index: 2;
  pointer-events: none;
}
.main-video--ready #player::before {
  left: 0;
  background: linear-gradient(270deg, rgba(0, 0, 0, 0) 0%, black 100%);
}
.main-video--ready #player::after {
  right: 0;
  background: linear-gradient(90deg, rgba(0, 0, 0, 0) 0%, black 100%);
}
.main-video #player {
  width: 100vw;
  max-width: 1800px;
}
@media (max-width: 1023.9px) {
  .main-video {
    height: 25vh;
  }
  .main-video--ready {
    height: 40vh;
    transition-property: opacity, background-color, height;
    transition-duration: 1s;
    background-color: black;
  }
  .main-video #player {
    transform: scale(1.7);
    min-height: 100% !important;
  }
}
@media (max-width: 767.9px) {
  .main-video {
    height: 25vh;
  }
  .main-video--ready {
    height: 40vh;
    transition-property: opacity, background-color, height;
    transition-duration: 1s;
    background-color: black;
  }
